.buttonGroup1{
   display: flex;
   justify-content: space-between;
   width: 100vw;
   
}

.modalImg{
   max-width: none !important
}

.pointer{
   cursor: pointer;
}
.textHtml{
   max-height: 38px;
   width: 174px;
   /* white-space: nowrap; */
   overflow: hidden;
   text-overflow: ellipsis;
}
.limitText{
   white-space: nowrap;
   display: flex;
   flex-direction: row;
   justify-content: end;
   /* overflow: hidden; */
   text-overflow: ellipsis;
}

.buttonFlex{
   width: 100%;
   display: flex;
   justify-content: space-evenly;
}

.buttonSub{
   padding: 6px 7px;
}
